@import url(https://fonts.googleapis.com/css2?family=Syne+Mono&display=swap);
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(24, 24, 32);
  color: white;
  font-size: 2vh;
  text-align: center;
}
.Tile {
  max-height: 100px;
  max-width: 100px;
  height: 13vw;
  width: 13vw;
  background-color: rgb(27, 18, 32);
  border-radius: 3px;
  box-shadow: 0 0 2px 1px rgb(27, 18, 32);
  transition: 0.2s;
}

.Tile-lit {
  background-color: rgb(255, 248, 252);
  box-shadow: 0 0 3px 1px rgb(255, 236, 247);
  transition: 0.1s;
}

.Tile:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.Board {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin: 80px auto;
  max-width: 530px;
  max-height: 530px;
  width: 70vw;
  height: 70vw;
  background-color: rgb(121, 108, 133);
  border-radius: 5px;
  box-shadow: 0 0 5px 2px rgb(121, 108, 133), 0 0 40px 5px rgb(255, 230, 234);
  /* animation: glow 1.7s ease-in-out infinite alternate; */
}

.h1-glow {
  font-size: 4vmax;
  letter-spacing: 0.4em;
  -webkit-animation: glow-text 2s ease-in-out infinite alternate;
          animation: glow-text 2s ease-in-out infinite alternate;
}

.Board-row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.Board-button {
  border: none;
  background-color: rgba(121, 108, 133, 0.5);
  font-family: 'Syne Mono', monospace;
  color: white;
  border-radius: 3px;
  padding: 0.5em 1em;
  width: 80px;
  margin: 30px auto;
  transition: 0.2s ease;
}

.Board-button:hover {
  background-color: rgba(121, 108, 133, 0.8);
}

/* @-webkit-keyframes glow {
  from {
    box-shadow: 0 0 7px 2px rgb(121, 108, 133), 0 0 30px 5px rgb(255, 230, 234), 0 0 40px 10px rgb(255, 230, 234), 0 0 50px 5px rgb(255, 230, 234);
  }
  to {
    box-shadow: 0 0 7px 2px rgb(121, 108, 133), 0 0 40px 5px rgb(255, 230, 234), 0 0 50px 10px rgb(255, 230, 234);
  }
} */

@-webkit-keyframes glow-text {
  from {
    color: rgb(121, 108, 133);
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ffcde6, 0 0 30px #ffcde6, 0 0 50px #ffcde6, 0 0 60px #ffcde6;
  }
  to {
    color: rgb(59, 52, 66);
    text-shadow: 0 0 10px #fff, 0 0 30px #ffcde6, 0 0 40px #ffcde6, 0 0 50px #ffcde6, 0 0 60px #ffcde6, 0 0 70px #ffcde6;
  }
}

/* TODO: responsive when turned the screen */
.App {
  font-family: 'Syne Mono', monospace;
  min-height: 100vh;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

